body {
  background-color: #CAF0F8;
  height: 100%;
  /* Make sure the body and HTML take up the full height of the viewport */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

.Logo {
  width: 118px;
}


.App {
  height: 100%;
  width: 100%;
}

.logo_div {
  position: fixed;
  top: 10px;
  margin-left: 20px;
}

.Contact {
  position: fixed;
  top: 10px;
  right: 100px;
}

.social {
  position: fixed;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 0;
  width: auto;
  height: auto;
  z-index: -3;
}

.Aboutbox {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 100px;
}

.Project {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 200px;
  right: 80px;
}




.Myskills {
  position: fixed;
  bottom: 10px;
  right: 100px;
}


.about {
  position: fixed;
  top: 200px;
  width: 100px;
  height: 200px;
}

@media (max-width: 450px) {

  .Contact{
    right: 20px;
  }

  .Logo {
    width: 50px;
  }

  .logo_div {
    margin-top: 10px;
    margin-left: 10px;
  }

  .about {
    position: fixed;
    top: 250px;
  }

  .Aboutbox {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  .Project {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 20%;
    right: 30px;
  }

  .about {
    top: 10px;
    left: -25px;
  }

}

@media (max-width: 840px) {

  .Logo {
    width: 50px;
  }

  .logo_div {
    margin-top: 10px;
    margin-left: 10px;
  }

  .about {
    position: fixed;
    left: -25px;
    top: 250px;
  }

  .Aboutbox {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    

  }

  .Project {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 20%;
    right: 15px;
  }




  @media (max-height: 820px) {

    .Myskills {
      bottom: 10%;
      right: -140px;
    }
  }


  @media (max-height: 766px) {

    .Aboutbox {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      margin-top: 100px;
    }

  }

  .about {
    top: 18%;
  }



}